@import '_variables';

.Loginone{
  position: relative;
  clear: both;
  display: block;
  width: 100%;
  float: left;
  margin-top: 0;
  padding: 60px 0;
  min-height: 100vh;
  @include border-radius(4px);
  .login-forms{
    @include box-shadow(0, 1px, 2px, rgba(0, 0, 0, 0.2));
    overflow: hidden;
    padding: 37px;
    .form-group{padding-bottom:20px;}
    
    label{
      color: $headingColor;
      font-weight: 500;
    }
    .select__single{
      height: 50px;
      border-radius: 4px;
      background-color: $white;
    }
    .checksss {
      float: left;
      display: inline-block;
      width: auto;
      margin-right: 7px;
      margin-left: 7px;
    }
  }      
}
